<template>
    <div>
        <Row type="flex" justify="center" align="middle">
            <i-col span="4">
                <Button type="success" long @click="create_new_topic">发布新错题</Button>
            </i-col>
            <i-col span="18" offset="2">
                <p>已发布错题：{{topic_num}}</p>
            </i-col>
        </Row>

        <div :style="{paddingTop: '20px'}"></div>

        <Table border :columns="col_style" :data="col_data"></Table>
    </div>
</template>

<script>
    import Data from "../../../../../../../assets/question-manage.json";

    export default {
        name: "home",

        data () {
            return {
                col_style: [
                    {
                        title: '题目ID',
                        key: 'id',
                        sortable: true
                    },
                    {
                        title: '标题',
                        key: 'title',
                        sortable: true
                    },
                    {
                        title: '标签',
                        key: 'tags'
                    },
                    {
                        title: '创建日期',
                        key: 'createTime',
                        sortable: true,
                        render: (h, params) => {
                            return h('div',
                                this.parse_time(params.row.createTime)
                            );
                        }
                    },
                    {
                        title: '操作',
                        key: 'action',
                        width: 300,
                        align: 'center',
                        render: (h, params) => {
                            return h('div', [
                                h('Button', {
                                    props: {
                                        type: 'info',
                                        size: 'default'
                                    },
                                    style: {
                                        marginRight: '10px'
                                    },
                                    on: {
                                        click: () => {
                                            this.show(params.index)
                                        }
                                    }
                                }, '查看'),
                                h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'default'
                                    },
                                    style: {
                                        marginRight: '10px'
                                    },
                                    on: {
                                        click: () => {
                                            this.edit(params.index)
                                        }
                                    }
                                }, '编辑'),
                                h('Button', {
                                    props: {
                                        type: 'warning',
                                        size: 'default'
                                    },
                                    on: {
                                        click: () => {
                                            this.remove(params.index)
                                        }
                                    }
                                }, '删除')
                            ]);
                        }
                    }
                ],
                col_data: Data
            }
        },
        methods: {
            create_new_topic() {
                this.$router.push({name: 'question_manage_edit', query: {id: -1}})
            },

            show (index) {
                this.$router.push({name: 'question_manage_view', query: {id: index}})
            },
            edit (index) {
                this.$router.push({name: 'question_manage_edit', query: {id: index}})
            },
            remove (index) {
                this.col_data.splice(index, 1);
            },

            parse_time: function (sec) {
                if (typeof sec === 'number') {
                    let date = new Date(sec);
                    return date.getFullYear() + "/" + date.getMonth() + "/" + date.getDay() + " " + date.getHours() + ":" + date.getMinutes() + ":" + date.getSeconds();
                } else {
                    return sec;
                }
            }
        },

        computed: {
            topic_num: function () {
                return this.col_data.length;
            }
        }
    }
</script>

<style scoped>

</style>



