<template>
    <div>
        <Row type="flex" align="middle">
            <i-col span="4">
                <Button type="success" long @click="add_new_user">添加新用户</Button>
            </i-col>
            <i-col span="2" offset="2">
                <p>当前用户数：{{user_num}}</p>
            </i-col>
            <i-col span="16">
                <Row type="flex" justify="end">
                    <Button type="primary" @click="disable_users">禁用所选用户</Button>
                    <span :style="{paddingLeft: '10px'}"></span>
                    <Button type="primary" @click="enable_users">恢复所选用户</Button>
                    <span :style="{paddingLeft: '10px'}"></span>
                    <Button type="warning" @click="delete_users">删除所选用户</Button>
                </Row>
            </i-col>

        </Row>

        <div :style="{paddingTop: '20px'}"></div>

        <Table border ref="selection" :columns="col_style" :data="col_data"
               @on-selection-change="selection_row_changed"></Table>
    </div>
</template>

<script>
    import UserData from '../../../../../assets/user-manage.json'

    export default {
        name: "user-manage",

        data() {
            return {
                col_style: [
                    {
                        type: 'selection',
                        width: 60,
                        align: 'center'
                    },
                    {
                        title: '用户ID',
                        key: 'id',
                        sortable: true
                    },
                    {
                        title: '用户名',
                        key: 'username',
                        sortable: true
                    },
                    {
                        title: '当前状态',
                        key: 'valid',
                        align: 'center',
                        render: (h, params) => {
                            if (params.row.valid) {
                                return h("div", "正常")
                            } else {
                                return h("div", "禁用")
                            }
                        },
                        sortable: true,
                        filters: [
                            {
                                label: '正常',
                                value: 1
                            },
                            {
                                label: '禁用',
                                value: 2
                            }
                        ],
                        filterMultiple: false,
                        filterMethod (value, row) {
                            if (value === 1) {
                                return row.valid === true;
                            } else if (value === 2) {
                                return row.valid === false;
                            }
                        }
                    },
                    {
                        title: '账号创建日期',
                        key: 'createTime',
                        render: (h, params) => {
                            return h("div",
                                this.parse_time(params.row.createTime)
                            );
                        },
                        sortable: true
                    },
                    {
                        title: '错题数目',
                        key: 'num',
                        align: 'center',
                        sortable: true
                    },
                    {
                        title: '上次登录',
                        key: 'lastLoginTime',
                        render: (h, params) => {
                            return h("div",
                                this.parse_time(params.row.lastLoginTime)
                            );
                        },
                        sortable: true
                    },
                    {
                        title: '操作',
                        key: 'action',
                        width: 250,
                        align: 'center',
                        render: (h, params) => {
                            return h('div', [
                                h('Button', {
                                    props: {
                                        type: 'info',
                                        size: 'default'
                                    },
                                    style: {
                                        marginRight: '10px'
                                    },
                                    on: {
                                        click: () => {
                                            this.show(params.index)
                                        }
                                    }
                                }, '更新用户信息'),

                                h('Button', {
                                    props: {
                                        type: 'warning',
                                        size: 'default'
                                    },
                                    on: {
                                        click: () => {
                                            this.delete_user(params.index)
                                        }
                                    }
                                }, '删除')
                            ]);
                        }
                    }
                ],
                col_data: UserData,

                selection_rows: []
            }
        },

        methods: {
            parse_time: function (sec) {
                if (typeof sec === 'number') {
                    let date = new Date(sec);
                    return date.getFullYear() + "/" + date.getMonth() + "/" + date.getDay() + " " + date.getHours() + ":" + date.getMinutes() + ":" + date.getSeconds();
                } else {
                    return sec;
                }
            },

            add_new_user: function () {
                this.$router.push('/register');
            },

            delete_user: function (index) {
                this.$Modal.confirm({
                    title: '确认',
                    content: '<p>确认删除？</p>',
                    onOk: () => {
                        this.col_data.splice(index, 1);
                    },
                    onCancel: () => {
                        // this.$Message.info('Clicked cancel');
                    }
                });

            },

            selection_row_changed: function (rows) {
                this.selection_rows = rows;
            },

            disable_users: function () {
                if (this.selection_rows.length === 0) {
                    this.$Message.info("您未选择任何行！");
                    return;
                }
                for (let row of this.selection_rows) {
                    this.col_data = this.col_data.map((item) => {
                        if (item.id === row.id) item.valid = false;
                        return item;
                    })
                }
            },

            enable_users: function () {
                if (this.selection_rows.length === 0) {
                    this.$Message.info("您未选择任何行！");
                    return;
                }
                for (let row of this.selection_rows) {
                    this.col_data = this.col_data.map((item) => {
                        if (item.id === row.id) item.valid = true;
                        return item;
                    })
                }
            },

            delete_users: function () {
                if (this.selection_rows.length === 0) {
                    this.$Message.info("您未选择任何行！");
                    return;
                }
                this.$Modal.confirm({
                    title: '确认',
                    content: '<p>确认删除？</p>',
                    onOk: () => {
                        for (let row of this.selection_rows) {
                            this.col_data = this.col_data.filter((item) => {return item.id !== row.id;})
                        }
                    },
                    onCancel: () => {
                        // this.$Message.info('Clicked cancel');
                    }
                });

            },

            handleSelectAll (status) {
                this.$refs.selection.selectAll(status);
            }
        },

        computed: {
            user_num: function () {
                return this.col_data.length;
            }
        }
    }
</script>

<style scoped>

</style>



