<template>
    <div>
        <div ref="editor" style="text-align:left"></div>
        <!--<Button v-on:click="getContent">查看内容</Button>-->
        <!--<textarea>{{ xss_content }}</textarea>-->
        <!--<textarea>{{ editorContent }}</textarea>-->
    </div>
</template>

<script>
    import E from 'wangeditor'
    // import XSS from 'xss'

    // TODO: Filter XSS Attack

    export default {
        name: 'editor',
        props: ['html-data'],
        data() {
            return {
                editorContent: '',

                // xss_options: {
                //     whiteList: {
                //         p: ['style'],
                //         span: ['style'],
                //         blockquote: [],
                //         img: ['src', 'alt'],
                //         pre: [],
                //         code: [],
                //         ol: [],
                //         ul: [],
                //         br: [],
                //     }
                // },
            }
        },
        methods: {
            getContent: function () {
                alert(this.editorContent)

            }
        },
        computed: {
            // xss_content: function () {
            //     return XSS(this.editorContent, this.xss_options)
            //     // return this.editorContent
            // }

        },

        mounted() {
            var editor = new E(this.$refs.editor);
            editor.customConfig.uploadImgShowBase64 = true;  // 使用 base64 保存图片

            editor.customConfig.zIndex = 20;

            editor.customConfig.onchange = (html) => {
                this.editorContent = html;
                this.$emit("textChange", html);
            };
            editor.create();

            editor.cmd.do('insertHTML', this.htmlData);

        }
    }
</script>

<style scoped>
</style>



