<template>
    <div class="nav_header">
        <!--<span style="color: #fff;">This is header.</span>-->
        <!--<Button type="text">登录</Button>-->
        <div class="nav_wrapper">
            <Row>
                <i-col span="4">
                    <span class="title">我的错题本</span>
                </i-col>

                <i-col span="17">
                    <Input v-model="value" size="large" placeholder="输入标签开始检索" class="input-search-box"/>
                    <Button size="large" type="primary" icon="ios-search" shape="circle" @click="search">搜索</Button>
                </i-col>

                <i-col span="3">
                    <Row type="flex" justify="end">
                        <router-link v-if="$route.path === '/login'" to="register">
                            <Button type="default">快速注册</Button>
                        </router-link>

                        <router-link v-else-if="$route.path === '/register'" to="login">
                            <Button type="success">已有账号？立即登录</Button>
                        </router-link>

                        <Poptip v-else trigger="hover" placement="bottom" :transfer=true>
                            <a class="nav_button_text">Username</a>
                            <div slot="content" class="pop-menu">
                                <div class="wrapper">
                                    <router-link to="notepad">
                                        <Button type="text" icon="android-person">个人空间</Button>
                                    </router-link>
                                </div>
                                <div class="wrapper">
                                    <router-link to="#">
                                        <Button type="text" icon="android-settings">设置</Button>
                                    </router-link>
                                </div>
                                <div class="wrapper">
                                    <router-link to="login">
                                        <Button type="text" icon="power">退出</Button>
                                    </router-link>
                                </div>
                            </div>
                        </Poptip>


                    </Row>

                </i-col>
            </Row>

        </div>

    </div>
</template>

<script>

    export default {
        name: "header",

        data: () => ({
            value: ""
        }),

        methods: {
            search: function () {
                if (this.value !== "") {
                    this.$router.push({name: 'Notepad', query: {word: this.value}});
                }
            }
        },


    }
</script>

<style scoped>

    .nav_header {
        color: #fff;
        margin: 0 auto;
        max-width: 1000px;
        min-width: 1000px;
    }

    .nav_wrapper {
        margin: 0 10px;
    }

    .nav_button_text {
        font-size: 12px;
    }

    .nav_button_text a {
        color: aliceblue;
    }

    .nav_button_text a:hover {
        color: #2d8cf0;
    }

    .search-box {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .title {
        font-size: 20px;
        padding-right: 50px;
    }

    .input-search-box {
        width: 500px;
        padding-right: 10px;
    }

    .pop-menu {
        text-align: center;
    }

</style>



