<template>
    <div>
        <Row type="flex" justify="center">
            <i-col span="16">
                <div class="edit-title">
                    <div>
                        <Row>
                            <i-col span="2">标题</i-col>
                            <i-col span="22">
                                <i-input v-model="edit_data.title" width="20"></i-input>

                            </i-col>
                        </Row>
                    </div>
                </div>

                <div class="rich-edit">
                    <editor :html-data="edit_data.qText" v-on:textChange="changeHtml"/>
                </div>

                <!--<textarea>{{ rich_text }}</textarea>-->

                <div class="wrapper-padding">

                    <Form class="form-body" ref="formValidate" :model="formValidate" :rules="ruleValidate"
                          :label-width="80">
                        <FormItem label="答案" prop="answer">
                            <i-input v-model="edit_data.cAnswer" placeholder="请输入参考答案"></i-input>
                        </FormItem>
                        <FormItem label="标签" prop="tags">
                            <Tag v-for="item in edit_data.qTag" :key="item" :name="item" closable @on-close="handleClose">{{item}}
                            </Tag>
                            <Button icon="ios-plus-empty" type="dashed" size="small" @click="handleAdd">添加标签</Button>
                        </FormItem>
                        <FormItem label="错题原因" prop="desc">
                            <i-input v-model="edit_data.reason" type="textarea" :autosize="{minRows: 2,maxRows: 5}"
                                     placeholder="填写备注"></i-input>
                        </FormItem>

                        <FormItem>
                            <Row type="flex" justify="space-between">
                                <i-col span="10">
                                    <Button type="primary" long @click="handleSubmit('formValidate')">提交</Button>
                                </i-col>

                                <i-col span="10">
                                    <Button type="default" long @click="cancelSubmit">放弃修改</Button>
                                </i-col>

                            </Row>
                        </FormItem>
                    </Form>
                </div>
            </i-col>
        </Row>

        <Modal v-model="set_tag" width="360">
            <p slot="header" style="text-align:center">
                <Icon type="plus-round"></Icon>
                <span>添加标签</span>
            </p>

            <div>
                <AutoComplete
                    v-model="edit_tag"
                    :data="tags"
                    @on-search="handleComplete"
                    placeholder="输入标签"
                >

                </AutoComplete>
            </div>

            <div slot="footer">
                <Button type="success" long @click="addTag">添加</Button>
            </div>
        </Modal>
    </div>
</template>

<script>
    import Editor from "./Editor";
    import Data from "../assets/data-edit.json";
    import Data_default from "../assets/data-edit-new.json"
    import Tags from "../assets/tags.json";

    export default {
        name: "edit-part",

        components: {
            Editor
        },

        data() {
            return {

                edit_data: Data,
                tags: [],

                set_tag: false,
                edit_tag: "",

                formValidate: {
                    answer: '',
                    desc: ''
                },

                ruleValidate: {
                    answer: [
                        {required: true, message: '答案不能为空', trigger: 'blur'}
                    ],
                },

                rich_text: '<p>在这里输入题目</p>'
            }
        },

        methods: {

            handleAdd: function () {
                this.set_tag = true;
            },

            // handleAdd() {
            //     this.$Modal.confirm({
            //         render: (h) => {
            //             return h('AutoComplete', {
            //                 props: {
            //                     value: "",
            //                     data: this.tags,
            //                     autofocus: true,
            //                     placeholder: '请输入标签'
            //                 },
            //                 on: {
            //                     input: (val) => {
            //                         // this.value = val;
            //                         this.edit_tag = val;
            //                         data = (val === "") ? [] :
            //                             this.tags.filter()
            //                         // this.$Message.success("a+  " + this.value);
            //                     },
            //
            //                 }
            //             })
            //         },
            //         onOk: () => {
            //             if (!this.edit_tag) {
            //                 this.$Message.error("未添加标签！");
            //             } else {
            //                 this.$Message.success("添加标签:" + this.edit_tag + " 成功！");
            //
            //                 this.tags.push(this.edit_tag);
            //             }
            //         }
            //     })
            // },
            handleClose(event, name) {
                const index = this.edit_data.qTag.indexOf(name);
                this.edit_data.qTag.splice(index, 1);
            },

            changeHtml: function (strHtml) {
                this.rich_text = strHtml;
            },

            handleComplete: function (value) {
                this.tags = !value ? [] : Tags.filter((tag) => {return tag.indexOf(value) >= 0});
            },

            handleSubmit: function (formValidate) {
                this.$router.push('item');
            },

            cancelSubmit: function () {
                this.$router.push('item');
            },

            addTag: function () {
                this.edit_data.qTag.push(this.edit_tag);
                this.set_tag = false;
            }
        },

        mounted() {
            if (this.$route.query.id === -1) {
                this.edit_data = Data_default;
            }
        }
    }
</script>

<style scoped>
    .form-body {
        text-align: left;

        padding-top: 20px;
    }

    .edit-title {
        color: #80848f;
        font-size: 20px;
        text-align: left;
        padding: 10px 0 20px;
    }

    .wrapper-padding {
        padding: 0 50px 0 20px;
    }
</style>



